<template>
  <div class="rater-list">

    <div class="page-wrap">

      <mt-tab-container class="page-tabbar-container" v-model="selected">

         <mt-tab-container-item id="会议心得">
             <mt-search cancel-text="取消" placeholder="输入会议心得主题" v-model="value" style="height:10vh" id="height1">
              <div v-for="item in filterResult1">
                <router-link :to="getUrl(item)" class="c">
                  <mt-cell
                    :title="item.meetingTitle" is-link
                    >
                    <span class="expireDay1" v-if="getColor(item.expireDay)">{{getPrompt(item.expireDay)}}</span>
                    <span class="expireDay2" v-else>{{getPrompt(item.expireDay)}}</span>
                  </mt-cell>
                  </router-link>
              </div>
              </mt-search>

              <div v-if="this.search=='false'">
                <div v-for="item in list1">
                  <router-link :to="getUrl(item)" class="c">
                    <mt-cell
                      :title="item.meetingTitle"
                      is-link>
                      <div class="sss">
                      <span class="expireDay1" v-if="getColor(item.expireDay)">{{getPrompt(item.expireDay)}}</span>
                      <span class="expireDay2" v-else>{{getPrompt(item.expireDay)}}</span>
                      </div>
                    </mt-cell>
                 </router-link>
                </div>
              </div>
        </mt-tab-container-item>

        <mt-tab-container-item id="两大课件">
             <mt-search cancel-text="取消" placeholder="输入两大课件主题" v-model="value" style="height:10vh" id="height2">
              <div v-for="item in filterResult2">
                <router-link :to="getUrl(item)" class="c">
                  <mt-cell
                    :title="item.meetingTitle"
                    >
                    <div class="sss">
                    <span class="expireDay1" v-if="getColor(item.expireDay)">{{getPrompt(item.expireDay)}}</span>
                    <span class="expireDay2" v-else>{{getPrompt(item.expireDay)}}</span>
                    </div>
                  </mt-cell>
                  </router-link>
              </div>
              </mt-search>
              <div v-if="this.search=='false'">
                <div v-for="item in list2">
                  <router-link :to="getUrl(item)" class="c">
                    <mt-cell
                      :title="item.meetingTitle"
                      is-link>
                      <div class="sss">
                        <span class="expireDay1" v-if="getColor(item.expireDay)">{{getPrompt(item.expireDay)}}</span>
                        <span class="expireDay2" v-else>{{getPrompt(item.expireDay)}}</span>
                      </div>
                    </mt-cell>
                 </router-link>
                </div>
              </div>

        </mt-tab-container-item>
      </mt-tab-container>
    </div>

    <mt-tabbar v-model="selected" fixed>

      <mt-tab-item id="会议心得" class="weui-tabbar__item ">
        <img src="../assets/2.png" alt class="weui-tabbar__icon">
        <p>会议</p>
      </mt-tab-item>
      <mt-tab-item id="两大课件" class="weui-tabbar__item ">
        <img src="../assets/3.png" alt class="weui-tabbar__icon">
        <p >两大课件</p>
      </mt-tab-item>
    </mt-tabbar>
  </div>
</template>

<script>
import server from '../assets/js/write.js'


export default {

  name: 'rater-list',
  data() {
    return {
      selected: '会议心得',
      badge:this.$store.state.user.badge,
     // badge: '080320013',
      meetingList:[{
          meetingTitle:'结合',
          expireDay:'1',
          meetingType:'会议心得',
          participantId:'111',
          commitTime:'',
        },


        {
          meetingTitle:'结合结合结合结合',
          expireDay:'1',
          meetingType:'两大课件心得',
          participantId:'111',
          commitTime:'',
        },

      ],
      value:'',
      search:'false'
    };
  },
  mounted(){

       var this2 = this;
       //console.log(this.badge+'111');
       server.write(this.badge).then(function(response) {
             console.log(response.data);
             this2.meetingList = response.data;
          }, function(response) {
              console.log(response);
          });
  },
  methods:{
    setHeight:function(){
      if(this.value.length>0){
        this.search = 'true';
        document.getElementById("height1").style.height="100vh";
        document.getElementById("height2").style.height="100vh";
      }
      else{
        this.search = 'false';
        document.getElementById("height1").style.height="10vh";
        document.getElementById("height2").style.height="10vh";
      }
    },
    getPrompt:function(day){
        if(day <= '0')
          return '写作超期';
        else
          return '剩余'+day+' 天';
    },
    getColor:function(day){
      if((day>-1&&day<=2)||day==-2)
        return true;
      else
        return false;
    },
    getUrl:function(item){
      return '/text?id='+item.participantId;
    }
  },
  watch: {
        'value': 'setHeight',
  },
  computed: {
    list1(){
        var company_result = new Array();
        var length = this.meetingList.length;
        for(var i = 0; i < length; i++) {
            if(this.meetingList[i].meetingType=='会议心得') {
              company_result.push(this.meetingList[i]);
            }
        }
          return company_result;
    },
    list2(){
        var company_result = new Array();
        var length = this.meetingList.length;
        for(var i = 0; i < length; i++) {
            if(this.meetingList[i].meetingType=='两大课件心得') {
              company_result.push(this.meetingList[i]);
            }
        }
          return company_result;
    },
    filterResult1() {
      var company_result = new Array();
      var length = this.list1.length;
    if(this.value.length>=1)
     {
        var filter = new RegExp(this.value,'i');
        for (var i = 0; i <= length - 1; i++) {
          var name = this.list1[i].meetingTitle;
          if(filter.test(name)){
             company_result.push(this.list1[i]);
          }
        }
      }
     return company_result;
  },
   filterResult2() {
      var company_result = new Array();
      var length = this.list2.length;
    if(this.value.length>=1)
     {
        var filter = new RegExp(this.value,'i');
        for (var i = 0; i <= length - 1; i++) {
          var name = this.list2[i].meetingTitle;
          if(filter.test(name)){
             company_result.push(this.list2[i]);
          }
        }
      }
     return company_result;
  }
}
};
</script>

<style scope>

.sss{
  width:110px;
}
.fontday{
  margin-left:30px;
  color:red;
}

.fontday2{
  margin-left:30px;
  color:#4ACB64;
}

.c{
  color:black;
}
  .page-tabbar {
    overflow: hidden;
    height: 100vh;
  }
  .page-wrap {
    overflow: auto;
    height: 100%;
    padding-bottom: 100px;
  }
  .weui-tabbar__icon {
    display: inline-block;
    width: 27px;
    height: 27px;
  }
  .expireDay1{
   display: inline-block;
    padding: .25em .4em;
    min-width: 8px;
    border-radius: 18px;
    background-color: #F43530;
    color: #FFFFFF;
    line-height: 1.2;
    text-align: center;
    font-size: 15px;
    vertical-align: middle;
    margin-left:30px;
  }
  .expireDay2{
    display: inline-block;
    padding: .25em .4em;
    min-width: 8px;
    border-radius: 18px;
    background-color:#4ACB64;
    color: #FFFFFF;
    line-height: 1.2;
    text-align: center;
    font-size: 15px;
    vertical-align: middle;
    margin-left:30px;
  }
.weui-tabbar__label {
    text-align: center;
    color: green;
    font-size: 12px;
    line-height: 1.8;
}
.weui-tabbar__item {
  background-color: #F2F4F3;
}
</style>



