import axios from 'axios'

const ServerAddress = 'https://open.auxgroup.com/api/auxhr/meetingrate/participants/'
//const ServerAddress = 'http://localhost:9999/participants/'

export default {
  
  write(badge) {
    return axios.get(ServerAddress + "writing/" + badge)
  },

  submit(badge) {
  	return axios.get(ServerAddress + "submit/" + badge)
  },

  //所有已经发了通知的心得，包括已经写完的和没写过的
  messaged(badge) {
    return axios.get(ServerAddress + "messaged/" + badge)
  },

  finishScore(badge) {
  	return axios.get(ServerAddress + "finishscore/" + badge)
  },

  //暂存心得文章
  save(participants) {
  	return axios.post(ServerAddress + "tempstore/" , participants)
  },

  commit(participants) {
  	return axios.post(ServerAddress + "commit/" , participants)
  },

  getthinking(id) {
    return axios.get(ServerAddress + "getthinking/" + id)
  }
}



